@media only screen and (max-width: 720px){
    .slick-list{
        // height: unset;
        min-height: 200px;
    }
    .slick-slider{
        padding: 10px 10px 40px 10px;
    }
    .slick-arrow{
        display: none !important;
    }

    .slick-dots li{
        height: 14px;
        width: 14px;
    }
    .slick-dots li button::before{
        height: 100%;
        width: 100%;
    }
}